/*=====================
    Chatting Scss start
==========================*/

.msger {
    margin-top: 105px;
}

.msg {
    display: flex;
    align-items: flex-end;
    margin-bottom: 10px;

    &:last-of-type {
        margin: 0;
    }
}

.msg-img {
    width: 30px;
    height: 30px;
    margin-right: 10px;
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    border-radius: 50%;

    [dir="rtl"] & {
        margin-left: 10px;
        margin-right: unset;
    }
}

.msg-bubble {
    max-width: 450px;
    padding: 8px;
    border-radius: 15px;
    background: var(--left-msg-bg);
    font-size: 16px;
}

.msg-info {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 10px;
}

.msg-info-name {
    margin-right: 10px;
    font-weight: bold;

    [dir="rtl"] & {
        margin-left: 10px;
        margin-right: unset;
    }
}

.msg-info-time {
    font-size: 0.85em;
}

.msger-chat {
    padding: 0 30px 120px;
}

.left-msg {
    .msg-bubble {
        border-bottom-left-radius: 0;
        background: rgba(var(--content-color), 0.25);
        border-radius: 6px;
        color: rgba(var(--title-color), 0.6);
        margin-right: 0;

        [dir="rtl"] & {
            margin-left: 0;
            margin-right: unset;
        }
    }
}

.right-msg {
    flex-direction: row-reverse;

    .msg-bubble {
        background: rgba(var(--theme-color), 1);
        border-radius: 6px 6px 0 6px;
        color: rgba(var(--white), 1);

        .msg-text {
            color: rgba(var(--dark-title), 1);
        }
    }

    .msg-img {
        margin-left: 10px;

        [dir="rtl"] & {
            margin-left: unset;
            margin-right: 10px;
        }

        .icon {
            width: 30px;
            height: 30px;
            border-radius: 100%;
        }
    }
}

.msger-inputarea {
    display: flex;
    position: fixed;
    bottom: 30px;
    left: 430px;
    width: calc(100% - 60px - 400px);
    gap: 10px;
    z-index: 1;

    .scan-icon {
        position: absolute;
        right: 130px;
        bottom: 20px;
    }

    .mic-icon {
        position: absolute;
        right: 90px;
        bottom: 20px;
    }

    .msger-send-btn {
        color: rgba(255, 255, 255, 1);
        font-weight: bold;
        background: rgba(var(--theme-color), 1);
        backdrop-filter: blur(2px);
        border-radius: 6px;
        width: 60px;
        height: 60px;
        display: flex;
        align-items: center;
        justify-content: center;
        border: none;
        .iconsax {
            svg {
                [stroke] {
                    fill: rgba(var(--dark-title), 1);
                    stroke: rgba(var(--theme-color), 1);
                }
            }
        }
    }

    .msger-input {
        padding: 10px 110px 10px 30px;
        border: none;
        border-radius: 8px;
        font-size: 20px;
        display: flex;
        align-items: center;
        justify-content: center;
        font-weight: 400;
        color: rgba(var(--title-color), 0.7);
        backdrop-filter: blur(5.5px);
        height: 60px;
        width: calc(100% - 60px - 10px);
        background: rgba(var(--content-color), 0.25);
        position: relative;

        &::placeholder {
            color: rgba(var(--content-color), 1);
        }

        &:focus {
            outline: none;
        }
    }
}

.no-chat {
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    text-align: center;
    img {
        mix-blend-mode: luminosity;
        opacity: 0.6;
    }
    h3 {
        font-size: 24px;
        color: rgba(var(--theme-color), 1);
        font-weight: 500;
        margin-top: 15px;
    }
}
