/*=====================
    Variables start
==========================*/
:root {
  --black: 0, 0, 0;
  --white: 255, 255, 255;
  --title-color: 245, 245, 245;
  --content-color: 110, 113, 121;
  --theme-color: 59, 237, 178;
  --dark-title: 13, 18, 31;
  --Iconsax-Color: #6e7179;
}

/*=====================
    Reset CSS start
==========================*/
@media (min-width: 1430px) {
  .container {
    max-width: 1400px;
  }
}
@media (max-width: 576px) {
  .container {
    padding-left: 15px;
    padding-right: 15px;
  }
}
section,
.section-t-space {
  padding-top: calc(40px + 60 * (100vw - 320px) / 1600);
}

.section-b-space {
  padding-bottom: calc(40px + 60 * (100vw - 320px) / 1600);
}

.section-mt-space {
  margin-top: calc(40px + 60 * (100vw - 320px) / 1600);
}

.section-mb-space {
  margin-bottom: calc(40px + 60 * (100vw - 320px) / 1600);
}

.text-content {
  color: rgba(var(--content-color), 1) !important;
}

.text-title {
  color: rgba(var(--title-color), 1) !important;
}

.text-theme-color {
  color: rgba(var(--theme-color1), 1) !important;
}

/*=====================
    Typography CSS start
==========================*/
* {
  margin: 0;
  padding: 0;
}

html {
  scroll-behavior: smooth;
}

body {
  position: relative;
  font-family: "Outfit", sans-serif;
  background: linear-gradient(0deg, #0a0e17, #0a0e17), linear-gradient(179.9deg, #0a5b75 -47.93%, #02111a 98.07%);
}
body:after {
  content: "";
  position: absolute;
  background-image: url(../images/about-bg.png);
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  opacity: 0.65;
  background-attachment: fixed;
  background-size: cover;
}
body.inner-bg:after {
  background-image: url(../images/inner-bg.png);
}

h1 {
  font-weight: 600;
  font-size: calc(22px + 34 * (100vw - 320px) / 1600);
  line-height: 1.5;
  letter-spacing: 0.03em;
}

h2 {
  font-size: calc(20px + 20 * (100vw - 320px) / 1600);
  font-weight: 600;
}

h3 {
  font-size: calc(18px + 2 * (100vw - 320px) / 1600);
  font-weight: 400;
}

h4 {
  font-size: calc(16px + 2 * (100vw - 320px) / 1600);
  font-weight: 400;
}

h5 {
  font-size: calc(15px + 1 * (100vw - 320px) / 1600);
  font-weight: 400;
}

h6 {
  font-size: 14px;
  font-weight: 400;
}

p {
  font-size: calc(14px + 4 * (100vw - 320px) / 1600);
  color: rgba(var(--content-color), 1);
  font-weight: 400;
}

a:not([href]):not([class]),
a:not([href]):not([class]):hover {
  color: rgba(var(--theme-color), 1);
}

a {
  text-decoration: none;
  color: rgba(var(--theme-color), 1);
}
a:hover {
  color: rgba(var(--theme-color), 1);
}

ul {
  padding-left: 0;
  margin-bottom: 0;
}
[dir=rtl] ul {
  padding-inline-start: 0;
  margin-inline-start: 0;
}

li {
  list-style: none;
  display: inline-block;
}

/*=====================
    Animation Scss start
==========================*/
@keyframes flip {
  from {
    transform: scaleX(-1);
  }
  to {
    transform: none;
  }
}
/*=====================
    Button Scss start
==========================*/
.btn {
  font-size: 22px;
  color: rgba(var(--theme-color), 1);
  font-weight: 500;
}

.btn-theme {
  position: relative;
  display: flex;
  text-align: center;
  font-size: calc(14px + 6 * (100vw - 320px) / 1600);
  background: rgba(var(--theme-color), 0.21);
  backdrop-filter: blur(6px);
  border-radius: 8px;
  padding: calc(10px + 2 * (100vw - 320px) / 1600) calc(20px + 20 * (100vw - 320px) / 1600);
}
.btn-theme span {
  margin: auto;
  color: rgba(var(--theme-color), 1);
}
.btn-theme:after {
  content: "";
  position: absolute;
  inset: 0;
  border-radius: 8px;
  background: linear-gradient(114.72deg, rgba(62, 255, 255, 0.5) -16.82%, rgba(58, 237, 177, 0.5) 59.74%);
  -webkit-mask: linear-gradient(#fff 0 0) content-box, linear-gradient(#fff 0 0); /* [2] */
  -webkit-mask-composite: xor;
  mask-composite: exclude;
  border: 1px solid;
  border-image-source: linear-gradient(114.72deg, rgba(62, 255, 255, 0.5) -16.82%, rgba(58, 237, 177, 0.5) 59.74%);
}
.btn-theme:hover {
  background: rgba(var(--theme-color), 0.21);
  box-shadow: 0 0 17px 3px rgba(52, 208, 156, 0.34);
}

.btn-arrow {
  color: rgba(var(--theme-color), 1);
  display: flex;
  align-items: center;
  gap: 15px;
  font-size: 18px;
  width: max-content;
}
.btn-arrow .icon-arrow {
  width: calc(36px + 6 * (100vw - 320px) / 1600);
  height: calc(36px + 6 * (100vw - 320px) / 1600);
  background-color: rgba(var(--theme-color), 1);
  border-radius: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 2px solid #0f141d;
  position: relative;
  transition: all 0.5s ease;
}
.btn-arrow .icon-arrow:after {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: rgba(var(--theme-color), 0.15);
  border-radius: 100%;
  right: -8px;
  transition: all 0.5s ease;
}
.btn-arrow .icon-arrow svg {
  stroke: rgba(var(--dark-title), 1);
  fill: rgba(var(--dark-title), 1);
  transform: rotate(45deg);
  width: calc(16px + 4 * (100vw - 320px) / 1600);
  transition: all 0.5s ease;
}
.btn-arrow:hover {
  color: rgba(var(--theme-color), 1);
}
.btn-arrow:hover .icon-arrow {
  transform: translateX(5px);
}
.btn-arrow:hover .icon-arrow:after {
  right: 0;
}
.btn-arrow:hover .icon-arrow svg {
  transform: rotate(90deg);
}

.btn-solid {
  font-size: calc(18px + 2 * (100vw - 320px) / 1600);
  color: rgba(var(--dark-title), 1);
  background-color: rgba(var(--theme-color), 1);
  border: 1px solid rgba(var(--theme-color), 1);
  display: inline-block;
  padding: calc(8px + 4 * (100vw - 320px) / 1600) calc(20px + 8 * (100vw - 320px) / 1600);
  border-radius: 8px;
  width: max-content;
  transition: all 0.5s ease;
}
.btn-solid:hover {
  background-color: transparent;
  color: rgba(var(--theme-color), 1);
}

/*=====================
    Breadcrumb Scss start
==========================*/
.breadcrumb-section {
  background-image: url("../images/breadcrumb-bg.png");
  background-position: bottom;
  background-size: cover;
  padding-top: 180px;
  padding-bottom: 30px;
  position: relative;
}
@media (max-width: 1400px) {
  .breadcrumb-section {
    padding-top: 150px;
  }
}
@media (max-width: 1199px) {
  .breadcrumb-section {
    padding-top: calc(80px + 30 * (100vw - 320px) / 1600);
  }
}
@media (max-width: 1199px) {
  .breadcrumb-section {
    padding-bottom: calc(40px + 20 * (100vw - 320px) / 1600);
  }
}
.breadcrumb-section .breadcrumb-content {
  display: flex;
  align-items: center;
  height: 100%;
}
.breadcrumb-section .breadcrumb-content h2 {
  display: flex;
  align-items: center;
  color: rgba(var(--theme-color), 1);
  font-weight: 600;
  gap: 5px;
  margin-bottom: 0;
}
.breadcrumb-section .breadcrumb-content h2 img {
  height: calc(50px + 30 * (100vw - 320px) / 1600);
}
.breadcrumb-section .breadcrumb-content p {
  margin-bottom: 0;
  padding-left: calc(20px + 20 * (100vw - 320px) / 1600);
  display: flex;
  gap: 10px;
}
.breadcrumb-section .breadcrumb-img {
  display: flex;
  justify-content: center;
  padding-bottom: 30px;
}
@media (max-width: 1400px) {
  .breadcrumb-section .breadcrumb-img {
    width: 80%;
    margin: 0 auto;
  }
}

/*=====================
    Chatting Scss start
==========================*/
.msger {
  margin-top: 105px;
}

.msg {
  display: flex;
  align-items: flex-end;
  margin-bottom: 10px;
}
.msg:last-of-type {
  margin: 0;
}

.msg-img {
  width: 30px;
  height: 30px;
  margin-right: 10px;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  border-radius: 50%;
}
[dir=rtl] .msg-img {
  margin-left: 10px;
  margin-right: unset;
}

.msg-bubble {
  max-width: 450px;
  padding: 8px;
  border-radius: 15px;
  background: var(--left-msg-bg);
  font-size: 16px;
}

.msg-info {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
}

.msg-info-name {
  margin-right: 10px;
  font-weight: bold;
}
[dir=rtl] .msg-info-name {
  margin-left: 10px;
  margin-right: unset;
}

.msg-info-time {
  font-size: 0.85em;
}

.msger-chat {
  padding: 0 30px 120px;
}

.left-msg .msg-bubble {
  border-bottom-left-radius: 0;
  background: rgba(var(--content-color), 0.25);
  border-radius: 6px;
  color: rgba(var(--title-color), 0.6);
  margin-right: 0;
}
[dir=rtl] .left-msg .msg-bubble {
  margin-left: 0;
  margin-right: unset;
}

.right-msg {
  flex-direction: row-reverse;
}
.right-msg .msg-bubble {
  background: rgba(var(--theme-color), 1);
  border-radius: 6px 6px 0 6px;
  color: rgba(var(--white), 1);
}
.right-msg .msg-bubble .msg-text {
  color: rgba(var(--dark-title), 1);
}
.right-msg .msg-img {
  margin-left: 10px;
}
[dir=rtl] .right-msg .msg-img {
  margin-left: unset;
  margin-right: 10px;
}
.right-msg .msg-img .icon {
  width: 30px;
  height: 30px;
  border-radius: 100%;
}

.msger-inputarea {
  display: flex;
  position: fixed;
  bottom: 30px;
  left: 430px;
  width: calc(100% - 60px - 400px);
  gap: 10px;
  z-index: 1;
}
.msger-inputarea .scan-icon {
  position: absolute;
  right: 130px;
  bottom: 20px;
}
.msger-inputarea .mic-icon {
  position: absolute;
  right: 90px;
  bottom: 20px;
}
.msger-inputarea .msger-send-btn {
  color: rgb(255, 255, 255);
  font-weight: bold;
  background: rgba(var(--theme-color), 1);
  backdrop-filter: blur(2px);
  border-radius: 6px;
  width: 60px;
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: center;
  border: none;
}
.msger-inputarea .msger-send-btn .iconsax svg [stroke] {
  fill: rgba(var(--dark-title), 1);
  stroke: rgba(var(--theme-color), 1);
}
.msger-inputarea .msger-input {
  padding: 10px 110px 10px 30px;
  border: none;
  border-radius: 8px;
  font-size: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 400;
  color: rgba(var(--title-color), 0.7);
  backdrop-filter: blur(5.5px);
  height: 60px;
  width: calc(100% - 60px - 10px);
  background: rgba(var(--content-color), 0.25);
  position: relative;
}
.msger-inputarea .msger-input::placeholder {
  color: rgba(var(--content-color), 1);
}
.msger-inputarea .msger-input:focus {
  outline: none;
}

.no-chat {
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  text-align: center;
}
.no-chat img {
  mix-blend-mode: luminosity;
  opacity: 0.6;
}
.no-chat h3 {
  font-size: 24px;
  color: rgba(var(--theme-color), 1);
  font-weight: 500;
  margin-top: 15px;
}

/*=====================
    Custom cursor Scss start
==========================*/
#cursor {
  position: fixed;
  top: -5px;
  left: -5px;
  width: 10px;
  height: 10px;
  background-color: rgba(var(--theme-color), 1);
  border-radius: 50%;
  pointer-events: none;
  z-index: 999;
}

#cursor-border {
  --size: 20px;
  position: fixed;
  top: calc(var(--size) / -2);
  left: calc(var(--size) / -2);
  width: var(--size);
  height: var(--size);
  border-radius: 50%;
  box-shadow: 0 0 0 1px rgba(var(--theme-color), 1);
  pointer-events: none;
  transition: top 0.15s ease-out, left 0.15s ease-out, width 0.15s ease-out, height 0.15s ease-out, background-color 0.15s ease-out;
  z-index: 999;
}

@media (max-width: 768px) {
  #cursor {
    display: none;
  }
  #cursor-border {
    display: none;
  }
}
/*=====================
    form Scss start
==========================*/
.input-group > .form-control:focus, .input-group > .form-control:focus-within,
.input-group > .form-floating:focus,
.input-group > .form-floating:focus-within,
.input-group > .form-select:focus,
.input-group > .form-select:focus-within {
  box-shadow: none;
  z-index: unset;
}

.auth-form input[type=number]::-webkit-inner-spin-button,
